.about-container {
    height: 99vmin;
    display: flex;
    flex-direction: row;

}

#video-about {
    max-height: 100vh;
    max-width: 50vw;
    overflow:hidden;
}

.svg {
    width: 3vmax;
    height: 2.5vmax;
    margin: 0.5vmax;
}

.icon {
    display: flex;
    flex-direction: row;
}

.icon-container{
    font-size: 2.5vmax;
    padding: 2vmax;
}

a:link {
    text-decoration: none;
    color: black;
}

a:visited {
    text-decoration: none;
    color: black;
}

a:hover {
    text-decoration: none;
    color: #4758FF;
}

a:active {
    text-decoration: none;
    color: #4758FF;
}

.text-container {
    margin-left: 2vmax;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 50vw;
}

.text {
    text-align: justify;
    padding-right: 2vmax;
    padding-left: 2vmax;
}

footer{
    font-size: 1vmax;
    color: #404040;
    padding-top: 0.5vmax;
}